.App-BotResponse {
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100vw;
}

.App-BotResponse-Activity {
  background-color: transparent;
  border-radius: 2vmin;
  color: gray;
  font-size: 4vmin;
  margin: 4vmin 4vmin 0;
  padding: 2vmin 3vmin 2vmin;
}

.App-BotResponse-Attachments {
  list-style-type: none;
  margin: 0;
  zoom: 2;
}

.App-BotResponse-Attachments .App-BotResponse-Attachment {
  background-color: White;
  border-radius: 1vmin;
  margin: 2vmin 0 2vmin 2vmin;
  width: auto;
  max-width: 72vw;
}

.App-BotResponse-Attachments li:last-child {
  padding-right: 2vmin;
}


/* 毛玻璃效果附件卡片 */
.App-BotResponse-Attachments .Glassmorphic-Attachment {
  /* 形状 */
  /* height: 80vh; */
  /* width: 80vw; */
  /* Adding our borders*/
  border-radius: 3%;
  border: 0px solid rgba( 255, 255, 255, 0.18 );
  

  /* 颜色质感透明度 */
  background: rgba( 255, 255, 255, 0.8 );
  backdrop-filter: blur( 7.5px );
  -webkit-backdrop-filter: blur( 7.5px );
  /*Adding the shadow*/ 
  box-shadow: 0 1px 4px 0 rgba( 0, 0, 0, 0.18 );

  /* 内容 */
  justify-content: space-between;

  /* 位置 */
  display: flex;
  position: relative;
  align-items: center;
  padding: 50px;
  margin: 2vmin 0 2vmin 2vmin;margin: 2vmin 0 2vmin 2vmin;
  z-index: 0;
}