/******************************************************************
 * 半透明薄片效果 *
*******************************************************************/

.App-MicrophoneButton {
  
  /* 形状 */
  width: 16vmin;
  height: 16vmin;
  border-radius: 50%;
  border: solid .4vmin black;

  /* 颜色透明度 */
  background-color: rgba(0, 0, 0, 1);
  opacity: .5;
  
  /* 内容 */
  justify-content: center;
  font-size: 8vmin;
  color: white;

  /* 位置 */
  right: 4vmin;
  bottom: 10vmin;
  margin: 0;
  z-index: 5;

  position: absolute;
  display: flex;
  overflow: hidden;
  align-items: center;
  
  outline: 0;
  
  /* The icon looks a bit off-center, shifting to compensate */
  padding-left: .5vmin;
  padding-top: .5vmin;
}

.App-MicrophoneButton:active,
.App-MicrophoneButton:hover {
  opacity: .8;
}

.App-MicrophoneButton > svg {
  fill: White;
}

.App-MicrophoneButton.dictating {
  background-color: #EF0000;
  border-color: #EF0000;
  opacity: 1;
}

.App-MicrophoneButton.dictating > svg {
  fill: White;
}


/******************************************************************
  毛玻璃效果
*******************************************************************/
.App-MicrophoneButton-Glass-Morphic {
  /* 基本 */
  /* display: flex; */
  /* overflow: overlay; */
  position: absolute;
  
  /* 形状 */
  width: 16vmin;
  height: 16vmin;
  z-index: 5;
  /* Adding our borders*/
  border-radius: 50%;
  border: solid 1px rgba( 255, 255, 255, 0.18 );

  /* 颜色质感透明度 */
  background: rgba( 255, 255, 255, 0.1 );
  backdrop-filter: blur( 10px );
  -webkit-backdrop-filter: blur( 10px );
  /*Adding the shadow*/
  box-shadow: 0 8px 32px 0 rgba( 0, 0, 0, 0.18 );

  /* 内容 */
  justify-content: center;
  color: rgba( 255, 255, 255 );

  /* 位置 */
  left:0; 
  right:0;
  margin:auto;
  bottom: 5%;
}


.App-MicrophoneButton-Glass-Morphic:active,
.App-MicrophoneButton-Glass-Morphic:hover {
  opacity: .8;
}

.App-MicrophoneButton > svg {
  fill: White;
}

.App-MicrophoneButton-Glass-Morphic.dictating {
  background-color: #EF0000;
  border-color: #EF0000;
  color: rgb(119, 119, 119);
  opacity: .8;
}

.App-MicrophoneButton-Glass-Morphic.dictating > svg {
  fill: White;
}